"use client";

import { useState } from "react";
import UploadBox from "../components/UploadBox";
import DocumentList from "../components/DocumentList";
import AskQuestionBox from "../components/AskQuestionBox";

export default function Page() {
  const [selectedDocId, setSelectedDocId] = useState<string | null>(null);
  const [refresh, setRefresh] = useState(false);

  return (
    <main className="min-h-screen bg-gray-100 p-6 flex flex-col gap-6 md:flex-row md:items-start md:justify-center">
      <div className="max-w-md w-full space-y-6">
        <UploadBox onUploadSuccess={() => setRefresh(!refresh)} />
        <DocumentList
          key={refresh.toString()}
          onSelect={(id) => setSelectedDocId(id)}
        />
      </div>

      <div className="max-w-md w-full">
        {selectedDocId ? (
          <AskQuestionBox documentId={selectedDocId} />
        ) : (
          <div className="p-6 bg-white rounded-2xl shadow-md text-center text-gray-500">
            Select a document to ask questions.
          </div>
        )}
      </div>
    </main>
  );
}
