"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Loader2 } from "lucide-react";
import { toast } from "sonner";

interface AskQuestionBoxProps {
  selectedDocId: string | null;
}

export default function AskQuestionBox({ selectedDocId }: AskQuestionBoxProps) {
  const [question, setQuestion] = useState("");
  const [response, setResponse] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleAskQuestion = async () => {
    if (!selectedDocId) {
      toast.error("Please select a document first!");
      return;
    }
    if (!question.trim()) {
      toast.error("Please enter a question!");
      return;
    }

    setLoading(true);
    setError(null);
    setResponse(null);

    try {
      const res = await fetch("http://127.0.0.1:8000/api/qa/query", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          document_id: selectedDocId,
          query: question,
        }),
      });

      if (!res.ok) {
        throw new Error(`Server error: ${res.status}`);
      }

      const data = await res.json();
      setResponse(JSON.stringify(data, null, 2)); // Pretty print JSON
      toast.success("Answer received!");
    } catch (err: any) {
      console.error("Error fetching answer:", err);
      setError("Failed to fetch the answer. Please try again.");
      toast.error("Error fetching answer");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card className="mt-6 shadow-md rounded-2xl border border-gray-200">
      <CardContent className="p-6 space-y-4">
        <h2 className="text-lg font-semibold text-gray-800">
          Ask a Question about this Document
        </h2>

        <Input
          placeholder="Type your question here..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          className="border-gray-300 focus:ring-2 focus:ring-blue-500"
        />

        <Button
          onClick={handleAskQuestion}
          disabled={loading}
          className="bg-blue-600 hover:bg-blue-700 text-white w-full rounded-lg"
        >
          {loading ? (
            <>
              <Loader2 className="w-4 h-4 mr-2 animate-spin" /> Getting Answer...
            </>
          ) : (
            "Ask Question"
          )}
        </Button>

        {error && (
          <div className="text-red-600 text-sm bg-red-50 border border-red-200 rounded-md p-3">
            {error}
          </div>
        )}

        {response && (
          <div className="bg-gray-50 border border-gray-200 rounded-md p-4 mt-4">
            <h3 className="text-sm font-semibold text-gray-700 mb-2">
              Response Body:
            </h3>
            <pre className="text-sm text-gray-800 overflow-auto whitespace-pre-wrap">
              {response}
            </pre>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
